<template>
  <div>
      <v-card>
        <v-card-title>
            <h3>Which Quiz Would You Like to View?</h3>
        </v-card-title>
        <v-card-text>
            <v-list>
                
                <router-link :to="{ path:'/viewquiz/0', params:'id' }">Quiz 1</router-link>
            </v-list>
        </v-card-text>
        <v-card-actions>
            <v-btn color="primary" :disabled="numInc < 1" @click="numInc--"><</v-btn>
            <v-btn color="primary" :disabled="allQuizzes.questions?numInc >= allQuizzes.answers.length-1:false" @click="numInc < allQuizzes.answers.length-1? numInc++:numInc = allQuizzes.answers.length-1">></v-btn>
        </v-card-actions>
      </v-card>
  </div>
</template>

<script>
export default {
    name:'viewquiz',
    data:()=>({
        numInc:0,
        adjustedQuizzes:[],
        allQuizzes:[],
        responseMsg:'',
    }),
    beforeMount(){
        this.viewQuiz()
    },
    methods:{
        adjustQuizzes(){
            if(this.numInc)
            this.numInc++
        },
        async viewQuiz(){
           let request = await this.$root.fetchData('POST', "/viewquiz", 
              {
                  username:this.$store.state.username
              }
            )
            this.allQuizzes = request
            this.responseMsg = request
        }
    }
}
</script>

